<template>
    <div id='edit-employee'>
        <h4 >Edit Employee</h4>
             <div class="row">
          <form @submit.prevent="updateEmployee" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input disabled type="text" v-model="employee_id" required >
                <!-- <label >Employee ID#</label> -->
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="firstName" required>
                <label class="active" >First Name</label>
              </div>
            </div>
              <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="lastName" required>
                <label class="active" >Last Name</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="company" required>
                <label class="active" >Company</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="address" required>
                <label class="active" >Address</label>
              </div>
            </div>

             <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="state" required>
                <label class="active" >State</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="zip" required>
                <label class="active"  >Zip</label>
              </div>
            </div>

             <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="email" required>
                <label class="active" >Email</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="phone" required>
                <label class="active" >Phone</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="buyerGroup" required>
                <label class="active" >Buyer Group</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="d_and_b_total" required>
                <label class="active" ># of EES</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="duns" required>
                <label class="active" >Ultimate DUNS #</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="role_bucket" required>
                <label class="active" >Role Bucket</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="leadInfo" required>
                <label class="active" >Lead Info</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="echo" required>
                <label class="active" >Echo Provided</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="sic_code" required>
                <label class="active" >SIC Code ID</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="renewalMonth" required>
                <label class="active" >Renewal Month</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="OBTM" required>
                <label class="active" >OBTM Vendor Comments</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="outcome" required>
                <label class="active" >Meeting Outcome</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="cig_sales" required>
                <label class="active" >Cigna Salesperson</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="currentCarrier" required>
                <label class="active" >Current Carrier</label>
              </div>
            </div>

            <button type="submit" class="btn">Submit</button>
            <router-link to="/" class="btn grey">Cancel</router-link>

          </form>
        </div>
            </li>
        </ul>
    </div>

</template>

<script>
/* eslint-disable */
import db from './firebaseInit'
export default {
    name: 'edit-employee',
  data() {
    return {
        id: null,
        employee_id:null,
        firstName: null,
        lastName:null,
        company:null,
        address:null,
        city:null,
        state:null,
        zip:null,
        email:null,
        phone:null,
        buyerGroup:null, 
        d_and_b_total:null,
        duns:null,
        role_bucket:null,
        leadInfo:null,
        echo:null,
        sic_code:null,
        renewalMonth:null,
        OBTM:null,
        outcome:null,
        cig_sales:null,
        currentCarrier:null
        
    }
  },
  beforeRouteEnter(to, from, next) {
      db.collection('employees-list').where('employee_id', '==', to.params.employee_id).get()
      .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              next(vm => {
                    vm.employee_id = doc.data().employee_id,
                    vm.id=doc.id,
                    vm.address= doc.data()["Address"],
                    vm.buyerGroup= doc.data()['Buyer Group'],
                    vm.city= doc.data()['City'],
                    vm.company= doc.data()['Company'],
                    vm.d_and_b_total=doc.data()['D & B Total Employees'],
                    vm.email= doc.data()['Email'],
                    vm.firstName= doc.data()["First Name"],
                    vm.lastName= doc.data()["Last Name"],
                    vm.phone= doc.data()['Phone'],
                    vm.role_bucket=doc.data()['Role Bucket'],
                    vm.sic_code= doc.data()['SIC Code ID'],
                    vm.state= doc.data()['State'],
                    vm.title= doc.data()['Title'],
                    vm.duns= doc.data()['Ultimage DUNS Number'],
                    vm.zip= doc.data()['ZIP Code'],
                    vm.leadInfo = doc.data()['Lead Information - Date & Time'],
                    vm.echo = doc.data()['Echo Provided'],
                    vm.sic_code = doc.data()['SIC Code ID'],
                    vm.renewalMonth = doc.data()['Renewal Month'],
                    vm.OBTM = doc.data()['OBTM Vendor Comments'],
                    vm.outcome = doc.data()['Meeting Outcome'],
                    vm.cig_sales = doc.data()['Cigna Salesperson'],
                    vm.currentCarrier = doc.data()['Current Carrier']
              })
          })
      })
  },
  watch: {
      '$route':'fetchData'
  },
  methods:{
      fetchData() {
          db.collection('employees-list').where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().employee_id
                    this.id=doc.id
                    this.address= doc.data()["Address"]
                    this.buyerGroup= doc.data()['Buyer Group']
                    this.city= doc.data()['City']
                    this.company= doc.data()['Company']
                    this.d_and_b_total=doc.data()['D & B Total Employees']
                    this.email= doc.data()['Email']
                    this.firstName= doc.data()["First Name"]
                    this.lastName= doc.data()["Last Name"]
                    this.phone= doc.data()['Phone']
                    this.role_bucket=doc.data()['Role Bucket']
                    this.sic_code= doc.data()['SIC Code ID']
                    this.state= doc.data()['State']
                    this.title= doc.data()['Title']
                    this.duns= doc.data()['Ultimage DUNS Number']
                    this.zip= doc.data()['ZIP Code']
                    this.leadInfo = doc.data()['Lead Information - Date & Time']
                    this.echo = doc.data()['Echo Provided']
                    this.sic_code = doc.data()['SIC Code ID ']
                    this.renewalMonth = doc.date()['Renewal Month']
                    this.OBTM = doc.data()['OBTM Vendor Comments']
                    this.outcome = doc.data()['Meeting Outcome']
                    this.cig_sales = doc.data()['Cigna Salesperson']
                    this.currentCarrier = doc.data()['Current Carrier']
              })
          })
      },
      updateEmployee() {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                 doc.ref.update({
                    employee_id: this.employee_id,
                    firstName: this.firstName,
                    lastName:this.lastName,
                    company:this.company,
                    address:this.address,
                    city:this.city,
                    state:this.state,
                    zip:this.zip,
                    email:this.email,
                    phone:this.phone,
                    buyerGroup:this.buyerGroup, 
                    d_and_b_total:this.d_and_b_total,
                    duns:this.duns,
                    role_bucket:this.role_bucket,
                    leadInfo:this.leadInfo,
                    echo:this.echo,
                    sic_code:this.sic_code,
                    renewalMonth:this.renewalMonth,
                    OBTM:this.OBTM,
                    outcome:this.outcome,
                    cig_sales:this.cig_sales,
                    currentCarrier:this.currentCarrier
                 })
                 .then(()=> {
                     this.$router.push({name:'view-employee', params: {employee_id: this.employee_id}})
                 })
              })
          })
      }
  }
  
}
</script>


<style scoped>
    * {
        font-size:0.9em;
    }
    h4 {
      font-size: 3rem;
  }
</style>



