<template>
    <div id='view-employee'>
         <router-link to="/" class="btn grey">Back</router-link>
       <!-- <button @click="deleteEmployee" class="btn red">Delete</button> -->
       <ul class="collection with-header">
           <li class="collection-header">
               <h4>
                   {{firstName}}, {{lastName}} <span class="company-text">:: {{company}}</span>
               </h4>
           </li>
           <li class="collection-item"><span class='label-text'>Address:</span> {{address}}</li>
           <li class="collection-item"><span class='label-text'>City:</span>  {{city}}</li>
           <li class="collection-item"><span class='label-text'>State:</span>  {{state}}</li>
           <li class="collection-item"><span class='label-text'>Zip:</span>  {{zip}}</li>
           <li class="collection-item"><span class='label-text'>Phone:</span>  {{phone}}</li>
           <li class="collection-item"><span class='label-text'>Email:</span>  {{email}}</li>
           <!-- <li class="collection-item"><span class='label-text'>Company:</span>  {{company}}</li> -->
           <li class="collection-item"><span class='label-text'>Role Bucket:</span>  {{role_bucket}}</li>
           <li class="collection-item"><span class='label-text'>Duns #:</span>  {{duns}}</li>
            <li class="collection-item"><span class='label-text'>D &amp; B Total Employess:</span>  {{duns}}</li>
            <li class="collection-item"><span class='label-text'>Buyer Group:</span>  {{buyerGroup}}</li>
            <li class="collection-item"><span class='label-text'>Lead Information - Date &amp; Time:</span>  {{leadInfo}}</li>
            <li class="collection-item"><span class='label-text'>Echo Provided:</span>  {{echo}}</li>
            <li class="collection-item"><span class='label-text'>SIC Code ID:</span>  {{sic_code}}</li>
            <li class="collection-item"><span class='label-text'>Renewal Month:</span>  {{renewalMonth}}</li>
            <li class="collection-item"><span class='label-text'>OTBM Vendor Comments:</span>  {{OTBM}}</li>
            <li class="collection-item"><span class='label-text'>Current Carrier:</span>  {{currentCarrier}}</li>
            <li class="collection-item"><span class='label-text'>Meeting Outcome:</span>  {{outcome}}</li>
            <li class="collection-item"><span class='label-text'>Cigna Salesperson:</span>  {{cig_sales}}</li>
       </ul>
       <router-link to="/" class="btn grey">Back</router-link>
       <button @click="deleteEmployee" class="btn red">Delete</button>

          <div class='fixed-action-btn'>
         <router-link v-bind:to="{name: 'edit-employee', params:{employee_id:employee_id}}" class='btn-floating btn-larg orange'>
             <i class='fa fa-pencil'></i>
         </router-link>
     </div>

    </div>
</template>

<script>
/* eslint-disable */
import db from './firebaseInit'

export default {
  name: 'view-employee',
  data () {
    return {
        id: null,
        employee_id:null,
        firstName: null,
        lastName:null,
        company:null,
        address:null,
        city:null,
        state:null,
        zip:null,
        email:null,
        phone:null,
        buyerGroup:null, 
        d_and_b_total:null,
        duns:null,
        role_bucket:null,
        leadInfo:null,
        echo:null,
        sic_code:null,
        renewalMonth:null,
        OBTM:null,
        outcome:null,
        cig_sales:null,
        currentCarrier:null
    }
  },
  beforeRouteEnter(to, from, next) {
      db.collection('employees-list').where('employee_id', '==', to.params.employee_id).get()
      .then(querySnapshot => {
          querySnapshot.forEach(doc => {
              next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.id=doc.id
                    vm.address= doc.data()["Address"]
                    vm.buyerGroup= doc.data()['Buyer Group']
                    vm.city= doc.data()['City']
                    vm.company= doc.data()['Company']
                    vm.d_and_b_total=doc.data()['D & B Total Employees']
                    vm.email= doc.data()['Email']
                    vm.firstName= doc.data()["First Name"]
                    vm.lastName= doc.data()["Last Name"]
                    vm.phone= doc.data()['Phone']
                    vm.role_bucket=doc.data()['Role Bucket']
                    vm.sic_code= doc.data()['SIC Code ID']
                    vm.state= doc.data()['State']
                    vm.title= doc.data()['Title'],
                    vm.duns= doc.data()['Ultimage DUNS Number'],
                    vm.zip= doc.data()['ZIP Code'],
                    vm.leadInfo = doc.data()['Lead Information - Date & Time'],
                    vm.echo = doc.data()['Echo Provided'],
                    vm.sic_code = doc.data()['SIC Code ID'],
                    vm.renewalMonth = doc.data()['Renewal Month'],
                    vm.OBTM = doc.data()['OBTM Vendor Comments'],
                    vm.outcome = doc.data()['Meeting Outcome'],
                    vm.cig_sales = doc.data()['Cigna Salesperson'],
                    vm.currentCarrier = doc.data()['Current Carrier']

              })
          })
      })
  },
  watch: {
      '$route':'fetchData'
  },
  methods:{
      fetchData() {
          db.collection('employees-list').where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                this.employee_id = doc.data().employee_id
                this.id=doc.id
                this.address= doc.data()["Address"]
                this.buyerGroup= doc.data()['Buyer Group']
                this.city= doc.data()['City']
                this.company= doc.data()['Company']
                this.d_and_b_total=doc.data()['D & B Total Employees']
                this.email= doc.data()['Email']
                this.firstName= doc.data()["First Name"]
                this.lastName= doc.data()["Last Name"]
                this.phone= doc.data()['Phone']
                this.role_bucket=doc.data()['Role Bucket']
                this.sic_code= doc.data()['SIC Code ID']
                this.state= doc.data()['State']
                this.title= doc.data()['Title']
                this.duns= doc.data()['Ultimage DUNS Number']
                this.zip= doc.data()['ZIP Code']
                this.leadInfo = doc.data()['Lead Information - Date & Time']
                this.echo = doc.data()['Echo Provided']
                this.sic_code = doc.data()['SIC Code ID ']
                this.renewalMonth = doc.date()['Renewal Month']
                this.OBTM = doc.data()['OBTM Vendor Comments']
                this.outcome = doc.data()['Meeting Outcome']
                this.cig_sales = doc.data()['Cigna Salesperson']
                this.currentCarrier = doc.data()['Current Carrier']
              })
          })
      },
      deleteEmployee() {
          if(confirm('Are you sure?')) {
               db.collection('employees-list').where('employee_id', '==', this.$route.params.employee_id).get()
             .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                  doc.ref.delete();
                  this.$router.push("/")
              })
          })
          }
      }

  }
}
</script>

<style scoped>
    .label-text {
        font-family: Arial, Helvetica, sans-serif;
        font-weight: 600;
        color:#555;
    }
    .company-text {
        font-size:1.5rem;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: 400;
        color:#555;
    }

    .btn {
        margin:1.5em 0;
    }
</style>




