<template>
    <nav>
        <div class="nav-wrapper orange">
            <div class="container">
                <router-link to='/' class="brand-logo left">Lead Generation List</router-link>
                <ul class="right">
                    <li v-if="isLoggedIn" ><span class="email white-text">Logged in as:</span> <span class="email black-text">{{currentUser}}</span></li>
                    <li>
                        <router-link v-if="isLoggedIn" to="/">Dashboard</router-link>
                   </li>
                      <li> <router-link v-if="!isLoggedIn" to="/login">Login</router-link></li>
                       <li><router-link v-if="!isLoggedIn" to="/register">Register</router-link></li>
                    <li> <button v-on:click="logout" v-if="isLoggedIn" class="btn black">Logout</button></li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
/* eslint-disable */
import firebase from 'firebase'

export default {
    name:"navbar",
    data() {
        return {
            isLoggedIn:false,
            currentUser:false
        }
    },
    created() {
        if(firebase.auth().currentUser){
            this.isLoggedIn = true
            this.currentUser = firebase.auth().currentUser.email
        }
    },

    methods:{
        logout() {
            firebase.auth()
            .signOut()
            .then(() => {
                this.$router.go({path:this.$router.path})
            })
    }
    }
}
</script>

<style scoped>
  email{
      padding-right:10px;
  }
</style>





