<template>
    <div id='dashboard'>
      <div class="collectin with-header">
        <div class="collection-header">
           <h4>Leads</h4>
        </div>
      </div>

      <div>
       <table class="striped responsive-table">
        <thead>
          <tr>
              <th>Role</th>
              <th>Name</th>
              <th>Company</th>
              <th>State</th>
              <th>Email</th>
              <th>Phone</th>
              <th>Detail</th>


          </tr>
        </thead>

        <tbody>
          <tr v-for="employee in employees" v-bind:key = "employee.employee_id">
            <td><div class="chip white-text orange lighten-1">{{employee.role_bucket}}</div></td>
            <td class="bolder">{{employee.firstName}}, {{employee.lastName}}</td>
            <td>{{employee.company}}</td>
            <td>{{employee.state}}</td>
            <td><span class="grey-text"><a :href="`mailto:${employee.email}`">{{employee.email}}</a></span></td>
            <td><span class="dark-grey-text">{{employee.phone}}</span></td>
            <td> <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id:employee.employee_id}}">
                    <i class="fa fa-eye" ></i>
                </router-link></td>
          </tr>  
            
        </tbody>
      </table>
      </div>
        <!-- <ul class="collection with-header">
            <li class="collection-header">
                <h4>Leads</h4>
            </li>
            <li v-for="employee in employees" v-bind:key = "employee.employee_id" class='collection-item'>
                <div class="chip white-text orange lighten-1">{{employee.role_bucket}}</div>
                 <span class="bolder"> {{employee.firstName}},{{employee.lastName}}</span> : <span class="grey-text">{{employee.phone}}</span> : <span class="grey-text"><a :href="`mailto:${employee.email}`">{{employee.email}}</a></span> : {{employee.company}}

                <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id:employee.employee_id}}">
                    <i class="fa fa-eye" ></i>
                </router-link>
            </li>
        </ul> -->

     <div class='fixed-action-btn'>
         <router-link to='/new' class='btn-floating btn-larg orange'>
             <i class='fa fa-plus'></i>
         </router-link>
     </div>
    </div>
</template>

<script>
/* eslint-disable */
  import db from './firebaseInit'
  export default {
    name: 'dashboard',
    data () {
      return {
        employees: [],
        loading: true
      }
    },
    created () {
      db.collection('employees-list').orderBy('ZIP Code').get().then((querySnapshot) => {
        this.loading = false
        querySnapshot.forEach((doc) => {
            // console.log(doc.data())
            // console.log(doc.id)
            // console.log(doc.data()["First Name"])
          const data = {
            'id': doc.id,
            'employee_id': doc.data()['employee_id'],
            'address': doc.data()["Address"],
            'buyerGroup': doc.data()['Buyer Group'],
            'city': doc.data()['City'],
            'company': doc.data()['Company'],
            'd_and_b_total':doc.data()['D & B Total Employees'],
            'email': doc.data()['Email'],
            'firstName': doc.data()["First Name"],
            'lastName': doc.data()["Last Name"],
            'phone': doc.data()['Phone'],
            'role_bucket':doc.data()['Role Bucket'],
            'sic_code': doc.data()['SIC Code ID'],
            'state': doc.data()['State'],
            'title': doc.data()['Title'],
            'duns': doc.data()['Ultimage DUNS Number'],
            'zip': doc.data()['ZIP Code'],
            'leadInfo': doc.data()['Lead Information - Date & Time'],
            'echo': doc.data()['Echo Provided']

          }
          this.employees.push(data)
        })
      })
    }
  }
</script>

<style scoped>

  * {
    font-size:0.8rem;
  }

  h4 {
    font-size: 3rem;
  }
  .bolder {
    font-family: Arial, Helvetica, sans-serif;
    font-weight:600;
  }
  .grey-text {
    color:#888;
  }
  
  .dark-grey-text {
    color:#555;
  }

  i {
    color:#777;
  }

  .fa-plus {
    color:#fff;
  }

  a {
    font-size:0.9rem;
    text-decoration: none;
    color:darkorange;
  }
</style>



