<template>
    <div id='new-employee'>
        <h3>New Employee</h3>
        <div class="row">
          <form @submit.prevent="saveEmployee" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="employee_id" required>
                <label >Employee ID#</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="firstName" required>
                <label >First Name</label>
              </div>
            </div>

             <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="lastName" required>
                <label >Last Name</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="company" required>
                <label >Company</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="address" required>
                <label >Address</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="city" required>
                <label >City</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="state" required>
                <label >State</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="zip" required>
                <label >Zip</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="email" required>
                <label >Email</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="phone" required>
                <label >Phone</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="d_and_b_total" required>
                <label ># of EES</label>
              </div>
            </div>
            
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="duns" required>
                <label >Ultimate DUNS #</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="leadInfo" required>
                <label >Lead Informaton - Date and Time</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="echo" required>
                <label >Echo Provided</label>
              </div>
            </div>
                  
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="sic_code" required>
                <label >SIC Code</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="renewalMonth" required>
                <label >Renewal Month</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="currentCarrier" required>
                <label >Current Carrier</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="OBTM" required>
                <label >OBTM Vendor Comments</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="outcome" required>
                <label >Meeting Outcome</label>
              </div>
            </div>

            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="cig_sales" required>
                <label >Cigna Salesperson</label>
              </div>
            </div>

            <button type="submit" class="btn">Add Lead</button>
            <router-link to="/" class="btn grey btn">Cancel</router-link>

          </form>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import db from "./firebaseInit"
export default {
  name: 'new-employee',
  data () {
    return {
        id: null,
        employee_id:null,
        firstName: null,
        lastName:null,
        company:null,
        address:null,
        city:null,
        state:null,
        zip:null,
        email:null,
        phone:null,
        buyerGroup:null, 
        d_and_b_total:null,
        duns:null,
        role_bucket:null,
        leadInfo:null,
        echo:null,
        sic_code:null,
        renewalMonth:null,
        OBTM:null,
        outcome:null,
        cig_sales:null,
        currentCarrier:null

    }
  },
  methods:{
    saveEmployee() {
      db.collection('employees-list').add({
        employee_id: this.employee_id,
        firstName: this.firstName,
        lastName:this.lastName,
        company:this.company,
        address:this.address,
        city:this.city,
        state:this.state,
        zip:this.zip,
        email:this.email,
        phone:this.phone,
        buyerGroup:this.buyerGroup, 
        d_and_b_total:this.d_and_b_total,
        duns:this.duns,
        role_bucket:this.role_bucket,
        leadInfo:this.leadInfo,
        echo:this.echo,
        sic_code:this.sic_code,
        renewalMonth:this.renewalMonth,
        OBTM:this.OBTM,
        outcome:this.outcome,
        cig_sales:this.cig_sales,
        currentCarrier:this.currentCarrier

      }).then(docRef=>this.$router.push('/')).catch(error => console.log(err))
    }
  }
}
</script>

<style scoped>
  .input-field>label {
    font-size: .75rem;
  }
   .row {
     height:35px;
   }
  .input-field {
    height:30px;
  }
  .btn {
    margin-top:2rem;
  }
</style>




